import React,{useState,useEffect} from 'react'
import axios from 'axios'
import { Link } from 'react-router-dom'

function BlogDetail(props) {
    const [blog,setBlog] = useState([])

    useEffect(()=>{
        const slug = props.match.params.id
        axios.get(`http://127.0.0.1:8000/api/blog/${slug}`)
        .then((response)=>{
            setBlog(response.data)
        })
        .catch((error)=>{
            console.log(error)
        })
    },[props.match.params.id])

    const createBlog = () =>{
        return {__html:blog.content}
    }

    const capitalizeFirstLetter = (word) =>{
        if(word)
            return word.charAt(0).toUpperCase() + word.slice(1)
        return ''
    }

    return (
        <div className='container mt-3'>
            <h1 className='display-2'>{blog.title}</h1>
            <h2 className='text-muted mt-3'>Category : {capitalizeFirstLetter(blog.category)} </h2>
            <h4>{blog.month} {blog.day}</h4>
            <div className='mt-5 mb-5' dangerouslySetInnerHTML={createBlog()}></div>
            <hr/>
            <p className='lead mb-5'><Link to='/blog' className='font-weight-bold'>Back to Blogs</Link></p>
        </div>
    )
}

export default BlogDetail
